import React from 'react'
import Link from 'gatsby-link'
import { rhythm, scale } from '../utils/typography'

import '../sass/s.scss'

import logo from '../assets/logo.svg'
import mmf from '../assets/training/mmf-logo-red.svg'

export default function Training() {
  return (
    <div className="creator-page teaching">
      <section className="myTeaching">
        <div className="article-wrapper">
          <header>
            <h1>Self-autonomy and personal achievement are the secret-sauce to long-term health and fitness success.</h1>
            <h2>Everyday, people make small sacrifices to their personal well-being due to a lack of self-autonomy. Not being able to reach a shelf, unable to climb the stairs, knee-pain keeping them on the couch, or a lack of energy to get out of bed and do&mdash;anything. This is where personal trainers can change lives.</h2>
          </header>
          <div className="fullWidthImage">
            <img src={mmf} alt="MinMaxFit Logo" />
          </div>
          <main>

            <div className="article-left">
              <p>Over at <a href="https://minmaxfit.com">MinMaxFit</a>, I'm helping people to better there lives and meet goals that only they can achieve.</p>
            </div>
          </main>
        </div>
      </section>
    </div>
  );
}

